/** account */
export class UserAccountInputArea extends StringInputArea<IUserAreaContext> {
	constructor(id: string = "account") {
		super(id);
		this.setLabel("Compte");
		this.setPattern("[a-zA-Z0-9\\.\\-\\*_@]+");
		this.setRequired(true);
		this.setVisible(userAreaIsVisible);
		this.setEnabled(userAreaIsEnabled);
		this.setInputCustomValidity(async function (this: HTMLInputElement, ctx: IUserAreaContext): Promise<string> {
			if (!this.checkValidity()) return "Seuls les caractères alpha-numériques et \".-*_@\" sont autorisés";
			if (ctx.usersSrv && (ctx.usersSrv as UsersSrv).getUser) {
				if (this.value) {
					let checkedUser = await (ctx.usersSrv as UsersSrv).getUser(this.value);
					if (checkedUser) {
						let code = this.value;
						return `L'identifiant d'utilisateur '${code}' a déjà été utilisé.`;
					}
				}
			}
			return "";
		}, 500, true)
	}

	_buildControl(ctx: IUserAreaContext, name: string): HTMLInputElement {
		let input = super._buildControl(ctx, name);
		DOM.setAttr(input, "spellcheck", "false");
		DOM.setAttr(input, "autocomplete", "current-password");// "off" ne fonctionne pas
		return input;
	}
}

REG.reg.registerSvc('area.users.account', 1, new UserAccountInputArea());

/** account et propriétés */
class UsesPropertiesArea extends LabelArea<IUserAreaContext> {

	constructor() {
		super("account");
		this.setLabel("Compte");
		this.setVisible(userAreaIsVisible);
		this.setEnabled(userAreaIsEnabled);
	}

	_buildControl(ctx: IUserAreaContext, name: string): Box {
		let control = <Box î={{skinOver: "area-users-props-box"} as OSkinableInit}/> as Box;
		const DTFormatter = new Intl.DateTimeFormat("fr", {year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric'});
		Object.assign(control, {
			fillJson(this: Box, parent: Jsonisable, root: Jsonisable): void {
				parent.account = this.dataset.account;
			},
			extractJson: function (this: Box, parent: JUser) {
				this.dataset.account = parent.account;
				let rootTag: HTMLDivElement = this.shadowRoot.querySelector("div");
				if (!rootTag) rootTag = this.shadowRoot.appendChild(<div/>) as HTMLDivElement;
				rootTag.innerHTML = "";
				let accountLine = rootTag.appendChild(<div/>);
				if (parent.isReadOnly)
					accountLine.appendChild(<span class="icon locked" title="Compte en lecture seule"/>);
				if (parent.isDisabled)
					accountLine.appendChild(<span class="icon disabled" title="Compte désactivé"/>);
				if (parent.isHidden && parent.userType === EUserType.user)
					accountLine.appendChild(<span class="icon system" title="Compte de service"/>);
				//accountLine.appendChild(<ButtonToggle onclick={function(this:ButtonToggle, ev:Event){this.toggleOn=!this.toggleOn}} î={{icon:"/@skin@/core/objects/user/system.svg", toggleOn:!parent.isHidden, uiContext:"bar"} as OButtonToggleInit}/>);
				accountLine.appendChild(
					<span class="account">{parent.account}</span>);
				if (parent.pwdDt) {
					let date = DTFormatter.format(parent.pwdDt);
					let txt = `Dernier changement de mot de passe : ${date}`;
					rootTag.appendChild(<div class="pwd">{txt}</div>);
				}
				return true;
			}
		} as IFormJsonisable);
		return control;
	}
}

