/** groups */
export class UserGroupsInputArea<C extends IUserAreaContext> extends ControlAsyncArea<C> implements IGridLineDrawer {
	constructor(id: string = "groups", public includeIsHiddenGroups: boolean | null = true, public checkGroupReentrancy = false, public loadDatasetOnInit = false) {
		super(id);
		this.setLabel("Groupes");
		this.setVisible((ctx) => {
			if (!ctx.usersSrv || !ctx.usersSrv.hasAspect(EUserAspects.groupable)) return false;
			return userAreaIsVisible.call(this, ctx);
		});
		this.setEnabled(userAreaIsEnabled);
	}

	async _loadControl(ctx: C, name: string): Promise<HTMLElement> {
		let inputBox = <div class="ctrlLbl"/>;
		// - Input
		const inputElt: InputOrderedSetPanel = inputBox.appendChild(new InputOrderedSetPanel().initialize({
			//init data déporté sur `extractJson` dans le cas de l'update de user (liste dépendant de la propriété 'flattenedGroups' du user courant)
			dataSet: this.loadDatasetOnInit ? await UserGroupsInputArea.fetchGroupsList(ctx, this.includeIsHiddenGroups) : {},
			selectionLabel: "Groupes sélectionnés",
			availableLabel: "Groupes disponibles",
			emptySelectionMsg: "Aucun",
			previewMode: "asLines",
			availableGridInit: {
				skinOver: "area-users-groups-tabs",
				lineDrawer: this,
			} as OGridSmallInit,
			selectionGridInit: {
				skinOver: "area-users-groups-tabs",
				lineDrawer: this,
			} as OGridSmallInit,
			name,
			checkValidity: this.checkGroupReentrancy ? function (this: InputOrderedSetPanel, selectedEntries) {
				// Contrôle de cycle dans les groupes via 'flattenedGroups'
				const incompatList: string[] = [];
				selectedEntries?.forEach(entry => {
					if (((inputElt.dataSet[entry] as any)?.flattenedGroups as string[])?.indexOf((this as any)._currentAccount) > -1)
						incompatList.push(inputElt.dataSet[entry].label)
				});
				if (incompatList.length) {
					const list: string = incompatList.join(", ");
					return `Groupe(s) incompatible(s) avec le groupe courant : '${list}'`
				} else
					return true;
			} : undefined,
		} as OOrderedSetInit));

		let addGroupBtn: IActionable<any>;

		const includeIsHiddenGroups = this.includeIsHiddenGroups;
		Object.assign(inputBox, {
			fillJson(this: Box, parent: Jsonisable, root: Jsonisable): void {
				parent[name] = inputElt.value;
			},
			extractJson: function (this: Box, parent: JUser) {
				// Recalcul du dataSet : les propriétés peuvent évoluer (modif du user courant, modif des 'flattenedGroups', ...)
				UserGroupsInputArea.fetchGroupsList(ctx, includeIsHiddenGroups).then((val) => {
					inputElt.dataSet = val;
				});
				(inputElt as any)._currentAccount = parent.account;
				inputElt.value = (parent as any)[name];
				if (addGroupBtn) addGroupBtn.disabled = parent.isReadOnly;
				return true;
			}
		} as IFormJsonisable);

		// maj de la zone input du formulaire "roles"
		inputElt.addEventListener("change", (evt) => {
			inputElt.form.querySelectorAll("c-input-roles-selector").forEach(async (elt: RolesSelectorInput) => {
				let inheritedRoles = await (ctx.usersSrv as UsersSrv).resolveRoles(inputElt.value);
				await elt.setInheritedRoles(inheritedRoles);
			});
			inputElt.reportValidity();
		});

		const defaultValue = this.getDefaultValue(ctx);
		if (defaultValue != null) inputElt.value = defaultValue;
		await this._initStdControl(inputElt, ctx);

		// - Btn add groupes
		let wrp = new ActionWrapper();
		wrp.setOverridenSvc(CreateUserAction.SINGLETON_group).setExecute(async (ctx: C, ev?: Event) => {
			let newGroup = await wrp.getWrapped(ctx).execute(ctx, ev);
			if (newGroup) {
				// FIXME : maj du dataset + ouverture du panel avec sélection de la nouvelle entrée "newGroup"
				inputElt.dataSet = await UserGroupsInputArea.fetchGroupsList(ctx, this.includeIsHiddenGroups);
			}
		});
		if (this.isAvailable(ctx)) addGroupBtn = inputBox.appendChild(ActionBtn.buildButton(wrp, ctx, "bar"));

		return inputBox;
	}

	static async fetchGroupsList(ctx: IUserAreaContext, includeIsHidden?: boolean | null): Promise<Dict<IOrderedSetEntry>> {
		const groupsSet = await (ctx.usersSrv as UsersSrv).list(null, EUserType.group, includeIsHidden, null, 100, null, null, ['flattenedGroups']);
		const groupsList = groupsSet.userList?.sort((groupA, groupB) => USER.getPrimaryName(groupA).localeCompare(USER.getPrimaryName(groupB)));
		let dataset: Dict<IOrderedSetEntry> = {};
		if (groupsList?.length > 0) {
			groupsList.forEach((pGroup) => {
				if (pGroup && (!ctx.users || !ctx.users.find((user) => user?.account === pGroup.account))) {
					const grps: string[] = pGroup.flattenedGroups?.filter(entry => entry != pGroup.account)
						.map(entry => {
							const userObj = groupsList.find((user => user.account == entry));
							return userObj ? USER.getPrimaryName(userObj) : entry;
						});

					dataset[pGroup.account] = {
						label: USER.getPrimaryName(pGroup),
						icon: "/@skin@/core/objects/group.svg",
						iconFilter: pGroup.isHidden ? "opacity(50%)" : undefined,
						description: grps?.length ? `groupes liés : ${grps}` : undefined,
						flattenedGroups: pGroup.flattenedGroups
					} as any;
				}
			});
		}
		return dataset;
	}

	redrawLine(row: GridDataRowJson<any>, line: HTMLElement): void {
		DOM.setAttrBool(line, "data-user-hidden", row.rowDatas.isHidden);
	}
}

