/** Gestion des groupes sur n users sélectionnés */
export class UserGroupsMultiInputArea<C extends IUserAreaContext> extends ControlAsyncArea<C> implements IGridLineDrawer {
	constructor(id: string = "groups", public includeIsHidden: boolean | null = true) {
		super(id);
		this.setLabel("Groupes");
		this.setVisible((ctx) => {
			if (!ctx.usersSrv || !ctx.usersSrv.hasAspect(EUserAspects.groupable)) return false;
			return userAreaIsVisible.call(this, ctx);
		});
		this.setEnabled(userAreaIsEnabled);
	}

	async _loadControl(ctx: C, name: string): Promise<HTMLElement> {
		let inputBox = <div class="ctrlLbl"/>;
		// - Input
		const inputElt = inputBox.appendChild(new InputOrderedSetPanel().initialize({
			dataSet: await UserGroupsInputArea.fetchGroupsList(ctx, this.includeIsHidden),
			selectionLabel: "Groupes sélectionnés",
			availableLabel: "Groupes disponibles",
			emptySelectionMsg: "Aucun",
			availableGridInit: {
				skinOver: "area-users-groups-tabs",
				lineDrawer: this,
			} as OGridSmallInit,
			selectionGridInit: {
				skinOver: "area-users-groups-tabs",
				lineDrawer: this,
			} as OGridSmallInit,
			name
		} as OOrderedSetInit));
		const defaultValue = this.getDefaultValue(ctx);
		if (defaultValue != null) inputElt.value = defaultValue;
		// Gestion de l'état indeterminate
		inputElt.indeterminate = true;
		inputElt.addEventListener("change", function (this: InputOrderedSetPanel, evt) {
			this.indeterminate = false;
		});
		inputElt.addEventListener('keydown', async function (this: InputOrderedSetPanel, ev: KeyboardEvent) {
			if (!this.disabled && (ev.key === 'Delete' || ev.key === 'Backspace') && this === DOMSH.findDocumentOrShadowRoot(this).activeElement) {
				this.indeterminate = true;
				this.value = null;
				ev.stopPropagation();
				this.form.dispatchEvent(new CustomEvent("change", {bubbles: true, cancelable: false}));
				return true;
			}
			return false;
		});

		// - Btn rôles
		/*
		let wrp = new ActionWrapper();
		wrp.setOverridenSvc(CreateUserAction.SINGLETON_group).setExecute(async (ctx: C, ev?: Event) => {
			let newGroup = await wrp.getWrapped(ctx).execute(ctx, ev);
			if (newGroup) {
				// FIXME : maj du dataset + ouverture du panel avec sélection de la nouvelle entrée "newGroup"
				// inputElt.dataSet = await UserGroupsInputArea.fetchGroupsList(ctx, this.filterHiddenGroups);
			}
		});
		inputBox.appendChild(ActionBtn.buildButton(wrp, ctx, "bar"));
		 */
		return inputBox;
	}

	redrawLine(row: GridDataRowJson<any>, line: HTMLElement): void {
		DOM.setAttrBool(line, "data-user-hidden", row.rowDatas.isHidden);
	}
}

