/** nickNames : gestion d'une *unique valeur* */
export class UserNicknamesFirstInputArea<C extends IUserAreaContext> extends StringInputArea<C> {
	constructor(id: string = "nickNames") {
		super(id);
		this.setLabel("Pseudo");
		this.setInputCustomValidity(async function (this: HTMLInputElement, ctx: IUserAreaContext): Promise<string> {
			if (this.value) {
				if (ctx.usersSrv && (ctx.usersSrv as UsersSrv).getUser) {
					if (ctx.users && ctx.users.length === 1 && ctx.users[0].nickNames && ctx.users[0].nickNames.indexOf(this.value) > -1)
						return "";
					let checkedUser = await (ctx.usersSrv as UsersSrv).getUser(this.value);
					if (checkedUser) {
						let code = this.value;
						return `L'identifiant d'utilisateur '${code}' a déjà été utilisé.`;
					}
				}
			}
			return "";
		}, 500, true)
		this.setVisible(userAreaIsVisible);
		this.setEnabled(userAreaIsEnabled);
	}

	isVisible(ctx: C): boolean {
		if (!ctx.userSelfSrv.hasAspect(EUserAspects.oneNickName)) return false;
		return super.isVisible(ctx);
	}

	_buildControl(ctx: C, name: string): HTMLInputElement {
		const control = super._buildControl(ctx, name);
		DOM.setAttr(control, "spellcheck", "false");
		DOM.setAttr(control, "autocomplete", "current-password");// "off" ne fonctionne pas
		Object.assign(control, {
			fillJson: function (this: HTMLInputElement, parent: Jsonisable, root: Jsonisable) {
				if (!this.disabled) parent[this.name] = this.value ? [this.value] : []; //Si disabled, nickName non éditable => on ne le retourne pas au serveur.
			},
			extractJson: function (this: HTMLInputElement, parent: Dict<any>) {
				const array = parent[this.name];
				if (array && array.length) {
					this.value = array[0];
					return true;
				} else
					this.value = null;//pour RAZ
				return false;
			}
		} as IFormJsonisable);
		return control;
	}
}

