/** isHidden, isDisabled */
class UsersBoolPropsInputArea extends InputChoiceArea<IUserAreaContext> {
	constructor(id: string) {
		super(id);
		this.setDefaultValue("false");
		this.setVisible(userAreaIsVisible);
		this.setEnabled(userAreaIsEnabled);
	}

	isEnabled(ctx: IUserAreaContext): boolean {
		//XXX A revoir : isEnabled n'est pas réévalué au changement du user à afficher dans l'area.
		// if (this._id === "isDisabled") {
		// 	//On interdit le user de se désactiver lui-même.
		// 	const currentUser = ctx.reg.env.universe.auth.currentAccount;
		// 	if (ctx.users.find((u) => u.account === currentUser)) return false;
		// }
		return super.isEnabled(ctx);
	}

	buildBody(ctx: IUserAreaContext): HTMLElement {
		let elt = super.buildBody(ctx);
		if (this.getDescription(ctx)) elt.title = this.getDescription(ctx);
		return elt;
	}

	_buildControl(ctx: any, name: string): HTMLDivElement {
		const control = super._buildControl(ctx, name);
		Object.assign(control, {
			fillJson: function (this: HTMLDivElement, parent: Jsonisable, root: Jsonisable) {
				let input = this.querySelector("input:checked") as HTMLInputElement;
				if (input) parent[name] = input.value === "true";
			},
			extractJson: function (this: HTMLDivElement, parent: Dict<any>) {
				if (parent[name])
					(this.querySelector("input[value='true']") as HTMLInputElement).checked = true;
				else
					(this.querySelector("input[value='false']") as HTMLInputElement).checked = true;
				return true;
			}
		} as IFormJsonisable);
		return control;
	}
}

