/**
 * Area de construction d'un double champs pour entrer un mot de passe
 */
export class PasswordInputArea<C extends IUserAreaContext> extends StringInputArea<C> {
	constructor(id?: string, public showLabelMode?: "placeholder") {
		super(id, "password");
		this.setLabel("Mot de passe");
		this.setVisible(userAreaIsVisible);
		this.setEnabled(userAreaIsEnabled);
	}

	buildBody(ctx: C): HTMLElement {
		const ctn = <div/>;
		if (!this.isVisible(ctx)) ctn.setAttribute('hidden', '');

		const pwdInput = this._buildControl(ctx, this.getId());
		DOM.setAttr(pwdInput, "autocomplete", "current-password");// "off" ne fonctionne pas
		if (this.showLabelMode === "placeholder") pwdInput.placeholder = this.getLabel(ctx);
		ctn.appendChild((this.showLabelMode !== "placeholder" && ctx.buildControlLabel) ? this._buildControlLabel(pwdInput, ctx) : <div class="ctrlLbl">{pwdInput}</div>);
		this._initStdControl(pwdInput, ctx);

		let pwdValidRequest = 0;
		if (this.isAvailable(ctx)) {
			pwdInput.addEventListener('input', () => {
				if (pwdValidRequest) {
					clearTimeout(pwdValidRequest);
					pwdValidRequest = 0;
				}
				pwdValidRequest = setTimeout(async () => {
					await this._checkPwd(pwdInput);
				}, 500) as any;
			});

			const confirmInput = this._buildControl(ctx, "confirmPwd");
			DOM.setAttr(confirmInput, "autocomplete", "current-password");
			this._initStdControl(confirmInput, ctx);
			if (this.showLabelMode === "placeholder") confirmInput.placeholder = "Confirmation";
			ctn.appendChild((this.showLabelMode !== "placeholder" && ctx.buildControlLabel) ? this._buildControlLabel(confirmInput, ctx, "Confirmation") : <div class="ctrlLbl">{confirmInput}</div>);

			Object.assign(confirmInput, {
				extractJson: function (this: HTMLInputElement, parent: Jsonisable) {
					//input technique (ni récupéré, ni renvoyé au serveur)
				},
				fillJson: function (this: HTMLDivElement, parent: Jsonisable, root: Jsonisable) {
					//input technique (ni récupéré, ni renvoyé au serveur)
				}
			});

			let confirmValidRequest = 0;
			confirmInput.addEventListener('input', () => {
				if (confirmValidRequest) {
					clearTimeout(confirmValidRequest);
					confirmValidRequest = 0;
				}
				confirmValidRequest = setTimeout(() => {
					if (confirmInput.value && confirmInput.value != pwdInput.value) confirmInput.setCustomValidity("Les mots de passe ne correspondent pas.");
					else confirmInput.setCustomValidity('');
				}, 300) as any;
			});

			pwdInput.addEventListener('input', () => {
				confirmInput.dispatchEvent(new CustomEvent("input", {bubbles: true, cancelable: false}));
			});
		}
		return ctn;
	}

	async _checkPwd(input: HTMLInputElement) {
		input.setCustomValidity('')
		if (input.value && input.checkValidity()) {
			const reg = REG.findReg<IBasicUniversePointer>(input);
			const resp = await reg.env.universe.userSelf.checkPwd({password: input.value});

			if (resp.result === "ok") {
				input.setCustomValidity('');
			} else if (resp.result == "failedInvalidDatas" && resp.secondaryResults && resp.secondaryResults.msgs) {
				input.setCustomValidity(resp.secondaryResults.msgs[0]);
			} else {
				ERROR.reportError({
					msg: "Une erreur est survenue lors de la vérification du mot de passe.",
					ctx: {reg},
					adminDetails: JSON.stringify(resp)
				});
			}
		}
	}
}

/** Hook sur le isVisible des areas de users */
export type IUserAreaIsVisibleHook = (this: ControlArea<any>, ctx: IUserBaseAreaContext) => boolean;

/** @return false si masqué
 * ctx "IUserBaseAreaContext" non utilisé car nécessite un recalcul du form lors de chaque sélection de user dans usersMgr.
 * A activer si le besoin apparait (init form userAreas dépendant de la liste des users)
 */
export function userAreaIsVisible(this: ControlArea<any>, ctx: IUserBaseAreaContext): boolean {
	let hook = ctx.reg.getSvc("user.area.visible.hook") as IUserAreaIsVisibleHook;
	if (hook) return hook.call(this, ctx);
	return null;
}

/** Hook sur le isEnabled des areas de users */
export type IUserAreaIsEnabledHook = (this: ControlArea<any>, ctx: IUserBaseAreaContext) => boolean;

/** @return false si disabled
 * ctx "IUserBaseAreaContext" non utilisé car nécessite un recalcul du form lors de chaque sélection de user dans usersMgr.
 * A activer si le besoin apparait (init form userAreas dépendant de la liste des users)
 */
export function userAreaIsEnabled(this: ControlArea<any>, ctx: IUserBaseAreaContext): boolean {
	let hook = ctx.reg.getSvc("user.area.enabled.hook") as IUserAreaIsEnabledHook;
	if (hook) return hook.call(this, ctx);
	return null;
}
